function Sliders() {
    let SlidersArray = []

    let sliders = document.querySelectorAll(".slider")
    sliders.forEach((slider,index) => {
        slider.setAttribute("data-slider-id",index)
        let isLoop = slider.classList.contains("_loop")
        let isNested = slider.classList.contains("_nested")

        SlidersArray[index] = new Swiper(`.slider[data-slider-id="${index}"] .swiper-container`, {
            slidesPerView: "auto",
            spaceBetween: 0,
            watchSlidesProgress: true,
            preventClicks :true,
            a11y: false,
            observer: true,
            observeParents: true,
            observeSlideChildren: true,
            loop: isLoop,
            nested: isNested,
            navigation: {
                nextEl: `.slider[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.slider[data-slider-id="${index}"] .swiper-button-prev`,
            },
            pagination: {
                el: `.slider[data-slider-id="${index}"] .swiper-pagination`,
                type: 'bullets',
                clickable: true,
            },
        })

        if(slider.classList.contains("use-slider2")){
            $(".use-slider .use-card").click(function () {
                let id = +$(this).attr("data-id")
                SlidersArray[index].slideTo(id,0)
                openModal("useModal")
            })
        }
    })
}

function gallery() {
    let SlidersArray = []

    let sliders = document.querySelectorAll(".gallery")
    sliders.forEach((slider,index) => {
        slider.setAttribute("data-slider-id",index)
        let gallery = {
            mySwiper: {},
            mySwiper2: {}
        }

        gallery.mySwiper = new Swiper(`.gallery[data-slider-id="${index}"] .mySwiper`, {
            spaceBetween: 0,
            slidesPerView: "auto",
            freeMode: true,
            watchSlidesProgress: true,
            direction: "vertical",
            breakpoints: {
                320: {
                    direction: "horizontal",
                },
                992: {
                    direction: "vertical",
                }
            }
        });
        gallery.mySwiper2 = new Swiper(`.gallery[data-slider-id="${index}"] .mySwiper2`, {
            spaceBetween: 0,
            slidesPerView: 1,
            direction: "vertical",
            navigation: {
                nextEl: `.gallery[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.gallery[data-slider-id="${index}"] .swiper-button-prev`,
            },
            thumbs: {
                swiper:  gallery.mySwiper,
            },
            breakpoints: {
                320: {
                    direction: "horizontal",
                },
                992: {
                    direction: "vertical",
                }
            }
        });

        SlidersArray[index] = gallery

        $("._toggle-gallery").click(function () {
            if(window.innerWidth<=992){
                return
            }
            $(".gallery-modal").toggleClass("_active")
            $("body").toggleClass("fixed-body")
        })

        $(".product-gallery .swiper-slide").click(function () {
            if(window.innerWidth<=992){
                return
            }

            let id = +$(this).attr("data-id")
            console.log(id)
            SlidersArray[index].mySwiper.slideTo(id,0)
            SlidersArray[index].mySwiper2.slideTo(id,0)
            $(".gallery-modal").addClass("_active")
            $("body").addClass("fixed-body")
        })
    })



}

function productSliders() {
    let SlidersArray = []

    let sliders = document.querySelectorAll(".collection-slider")
    sliders.forEach((slider,index) => {
        slider.setAttribute("data-slider-id",index)
        let isLoop = slider.classList.contains("_loop")

        SlidersArray[index] = new Swiper(`.collection-slider[data-slider-id="${index}"] .collection-slider__container`, {
            wrapperClass: "collection-slider__wrapper",
            slideClass: "collection-slider__slide",
            slidesPerView: "auto",
            spaceBetween: 0,
            watchSlidesProgress: true,
            preventClicks :true,
            a11y: false,
            observer: true,
            observeParents: true,
            observeSlideChildren: true,
            loop: isLoop,
            navigation: {
                nextEl: `.collection-slider[data-slider-id="${index}"] .collection-slider__nav .swiper-button-next`,
                prevEl: `.collection-slider[data-slider-id="${index}"] .collection-slider__nav .swiper-button-prev`,
            },
            scrollbar: {
                el: `.collection-slider[data-slider-id="${index}"] .swiper-scrollbar`,
                draggable: true,
                snapOnRelease: true,
                dragSize: 'auto',
            },
        })
    })
}

$(document).on('mouseover', '.product-card .swiper-pagination-bullet', function(e) {
    $(this).click()
})


Sliders()
gallery()
productSliders()
