$(".catalog-view__btn input").change(function () {
    setCatalogView()
})

setCatalogView()

function setCatalogView() {
    let checkedInput = $(".catalog-view__btn input:checked").attr("data-value")
    if(checkedInput === "_view2"){
        $(".catalog").addClass("_big-card-view")
    }else{
        $(".catalog").removeClass("_big-card-view")
    }
}

$("._show-date-filter").click(function () {
    $(".m-page").addClass("_open-sub-filter")
    $(".m-page").addClass("_open-date-filter")
})

$("._close-sub-filter").click(function () {
    $(".filter .filter-item").removeClass("_open")
    $(".m-page").removeClass("_open-sub-filter")
    $(".m-page").removeClass("_open-date-filter")
})

$("._toggle-filter").click(function () {
    document.body.scrollTop = document.documentElement.scrollTop = 0;

    $(".m-page").toggleClass("_open-filter")
    $(".m-page").toggleClass("fixed-body")
})

$("._toggle-catalog-menu").click(function () {
    document.body.scrollTop = document.documentElement.scrollTop = 0;

    $(".m-page").toggleClass("_open-catalog-menu")
    $(".m-page").toggleClass("fixed-body")
})

$(".filter .filter-item").each(function (i,el) {
    let options = $(el).find(".filter-option input")
    let count = $(el).find(".filter-item__count span")
    let toggle = $(el).find(".filter-item__show")

    $(toggle).click(function () {
        $(el).toggleClass("_open")

        if(window.innerWidth <= 992){
            $(".m-page").addClass("_open-sub-filter")
        }
    })
    $(el).find(".filter-item__btn").click(function () {
        $(el).removeClass("_open")
    })

    $(document).mouseup( function(e){
        let div = $(el);
        if ( !div.is(e.target)
            && div.has(e.target).length === 0 ) {
            if($(div).hasClass("_open")){
                $(div).removeClass("_open")
            }
        }
    });

    $(options).change(function () {
        setCount()
        setCurrent()
    })
    setCount()
    setCurrent()

    function setCount() {
        let checkedInputs = $(el).find(".filter-option input:checked").length

        if(checkedInputs > 0){
            $(el).addClass("_show-count")
            $(count).html(checkedInputs)
        } else{
            $(el).removeClass("_show-count")
        }
    }

    function setCurrent() {
        if($(el).hasClass("_radio")){
            let checked = $(el).find(".filter-option input:checked").attr("data-value")
            $(el).find(".filter-item__current").html(checked)
            $(el).removeClass("_open")
            $(".m-page").removeClass("_open-sub-filter")
        }
    }
})

$(".product-card").each(function (cardIndex,card) {
    $(card).find(".color-field input").each(function (radioIndex,radio) {
        $(radio).attr("name",`color${cardIndex}`)
    })
    $(card).find(".size-field input").each(function (radioIndex,radio) {
        $(radio).attr("name",`size${cardIndex}`)
    })
})
