$("._tab").click(function() {
    let parentBlock = $(this).parents("._tabs-parent")
    let tabId = $(this).attr("data-tab")
    $(parentBlock).find("._tab").removeClass("_active")
    $(this).addClass("_active")
    $(parentBlock).find(".tab-content").removeClass("_active")
    $(parentBlock).find(`.${tabId}`).addClass("_active")
})

$(document).on('click', '.rating-input__item', function(e) {
    let ratingItem = parseInt($(this).attr("data-rating-value"))
    $(this).parent(".rating-input").attr("data-total-rating", ratingItem)
    $(this).siblings(".rating-input__field").val(ratingItem)
})

$(".pass-toggle").click(function () {
    if($(this).hasClass("_active")){
        $(this).removeClass("_active")
        $(this).siblings("input").attr("type","password")
    } else{
        $(this).addClass("_active")
        $(this).siblings("input").attr("type","text")
    }
})

$(".qa-card__show").click(function () {
    if($(this).parents(".qa-card").hasClass("_active")){
        $(".qa-card").removeClass("_active")
        $(".qa-card__hidden").slideUp()
    } else{
        $(".qa-card").removeClass("_active")
        $(".qa-card__hidden").slideUp()
        $(this).parents(".qa-card").addClass("_active")
        $(this).siblings(".qa-card__hidden").slideDown()
    }
})

$(".favourite-btn").click(function () {
    $(this).toggleClass("_added")

    if(!$(this).hasClass("_added")){
        $(".m-page").append(`
        <div class="alert" id="favouriteAlert">
            <svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M6.70004 12L3.19995 8.49991L4.19977 7.50009L6.70075 9.99894L6.70004 9.99965L12.6997 4L13.6995 4.99982L7.69986 11.0002L6.70075 11.9993L6.70004 12Z" fill="#F4F2F5"/>
            </svg>
            <div class="alert__text text14">
                Товар удален из избранного
            </div>
        </div>
        `)

        setTimeout(()=>{
            $("#favouriteAlert").remove()
        },5000)
    }
})

$('.cartcalc').each(function (i,el) {
    let plus = $(el).find(".ccalc-plus")
    let minus = $(el).find(".ccalc-minus")
    let input = $(el).find("input")

    $(minus).click(function(e) {
        let a = $(input).val();
        if (a > 1) {
            let b = +a - 1;
            $(input).val(b);
            $(input).trigger('change');
        } else {
            $(input).val(a);
        }
        setMinusBtnClickability()
        e.stopImmediatePropagation()
    });

    $(plus).click(function(e) {
        let a = $(input).val();
        let b = +a + 1;
        $(input).val(b);
        $(input).trigger('change');
        setMinusBtnClickability(this)
        e.stopImmediatePropagation()
    });

    function setMinusBtnClickability() {
        let val = +$(input).val()
        if(val === 1){
            $(minus).addClass("_disabled")
        } else{
            $(minus).removeClass("_disabled")
        }
    }

    setMinusBtnClickability()
})
