$(".condition-card").click(function () {
    if($(this).hasClass("_active")){
        $(".condition-card").removeClass("_active")
        $(".condition-card").removeClass("_disable")
        if(window.innerWidth<=992){
            $(".condition-card__content2").slideUp()
        }
    } else{
        $(".condition-card").removeClass("_active")
        $(".condition-card").addClass("_disable")
        $(this).removeClass("_disable")
        $(this).addClass("_active")
        if(window.innerWidth<=992){
            $(".condition-card__content2").slideUp()
            $(this).find(".condition-card__content2").slideDown()
        }
    }
})

$(".condition-card__btn").click(function (e) {
    e.stopPropagation()
    openModal("reqModal")
})

let conditionMouseOutTimer;

$(".condition__grid").mouseleave(function () {
    conditionMouseOutTimer = setTimeout(function(){
        $(".condition-card").removeClass("_active")
        $(".condition-card").removeClass("_disable")
    }, 8000);
})

$(".condition__grid").mouseover(function () {
    clearTimeout(conditionMouseOutTimer);
})
